interface ButtonProps {
    onClick: () => void;
    disabled?: boolean;
    className?: string;
    children: React.ReactNode;
}

import React from 'react';

const handleClick = () => {
    alert('Button clicked!');
};

 const Button: React.FC<ButtonProps> = ({ onClick, disabled, className, children }) => {
    return (
        <button
            style={{
                color: 'white',
                backgroundColor: 'blue',
                height: 50,
                width: 50
            }}
            name="ButtonName"
            onClick={handleClick}
            disabled={disabled}
            className={className}
        >
            {children}
        </button>
    );
};

export default Button;